.dropdown {
    margin: 0 auto;
    z-index: 10000;
    width: 300px;
    position: relative;
}
.dropdown,
.dropdown * {
    z-index: 10;
}
.dropdown .dropdown-btn {
    cursor: pointer;
    background: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    color: #777;
    font-weight: 500;
}
.dropdown-content {
    position: absolute;
    left: 0;
    width: 100%;
    box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.07);
    background-color: #FFFFFF;
}
.dropdown-content .item {
    padding: 10px;
    cursor: pointer;
}
.dropdown-content .item:hover {
    background: #fcfcfc;
}
